export default {
	data() {
		return {
			list: [
				{'id':1, 'name':'店长推荐'},
				{'id':2, 'name':'新品上市'},
				{'id':3, 'name':'国外狠货'},
				{'id':4, 'name':'休闲零食'},
			],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			
			// 规格选择有关
			sizeCheckList:[
				{"id":1, "number":500},
				{"id":2, "number":1000},
				{"id":3, "number":2500}
			],
			sizeCheckId:"",
			
			// 记载商品数量
			shopNumber:1,
			
			
			// 模态框的弹出
			buyModel:false,
			
			WindowsHight:this.WindowsHight,
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		
	},
	onReady() {
		uni.hideLoading()
	},
	methods: {
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		showBuyModel(){
			this.buyModel = true
		},
		gotoShopInfor(){
			console.log(123)
			uni.navigateTo({
				url:'../index/shopping-infor'
			})
		},
		closeModel(){
			this.buyModel = false
		},
		sizeCheck(e){
			let id = e.currentTarget.dataset.id;
			this.sizeCheckId = id;
			
		},
		countMinus(){
			if( this.shopNumber < 2 ){
				uni.showToast({
					icon:'none',
					title:'数量不可以小于1'
				});
				return
			}
			this.shopNumber -= 1
		},
		countAdd(){
			this.shopNumber += 1
		}
		
		
		
	}
}
